.mint_count_button_selected {
  margin-left: 10px;
  width: 76px;
  height: 83px;
  left: 10px;
  top: 10px;
  float: left;
  cursor: pointer;

  background: #D9D9D9;
  border: 4px solid #90AAF1;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.mint_count_button_unselected {
  margin-left: 10px;
  width: 76px;
  height: 83px;
  left: 96px;
  top: 10px;
  float: left;
  cursor: pointer;

  border: 4px solid #90AAF1;
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.mint_count_button_selected_text {
  margin-left: 18px;
  width: 38px;
  height: 84px;
  left: 29px;
  top: 3px;

  font-family: Sen;
  font-style: normal;
  font-weight: normal;
  font-size: 4.375em;
  line-height: 84px;

  color: #000000;
}

.mint_count_button_unselected_text {
  margin-left: 18px;
  width: 38px;
  height: 84px;
  left: 115px;
  top: 3px;

  font-family: Sen;
  font-style: normal;
  font-weight: normal;
  font-size: 4.375em;
  line-height: 84px;

  color: #D4EFFF;
}